.btn-container {
  padding: 4px 12px; }
  .btn-container button {
    display: inline-flex;
    align-items: center;
    flex-shrink: 0;
    padding: 0 20px;
    width: 281px;
    height: 48px;
    background-color: rgba(0, 0, 0, 0.2);
    transition: background-color 0.3s ease;
    font-size: 0.9rem;
    font-weight: normal;
    text-shadow: none; }
    @media print {
      .btn-container button {
        display: none; } }
    @media (max-width: 640px) {
      .btn-container button {
        display: none; } }
    .btn-container button:hover {
      background-color: rgba(0, 0, 0, 0.3); }
    .btn-container button i.UI-icon.UI-plus {
      font-size: 1.2rem;
      position: relative;
      top: -1.5px;
      margin-right: 3px; }
