#sidebar {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  justify-content: center;
  position: absolute;
  bottom: 29px;
  right: 24px; }
  #sidebar button {
    margin: 4px; }
    #sidebar button i.UI-icon.UI-plus {
      top: -3px; }
    #sidebar button i.UI-icon {
      left: 1px; }
    #sidebar button[name="new-column"] {
      display: none; }
      @media (max-width: 640px) {
        #sidebar button[name="new-column"] {
          display: block; } }

.appear-enter, .appear-leave-to {
  opacity: 0;
  transform: scale(0); }

.appear-enter-active, .appear-leave-active {
  transition: transform 0.2s; }

.appear-enter-to, .appear-leave {
  opacity: 1;
  transform: scale(1); }

@media print {
  #sidebar {
    display: none; } }
