@media screen {
  main {
    background-color: #eaeff2;
    height: 100%; }
    main #columns {
      overflow-x: auto;
      -webkit-overflow-scrolling: touch;
      touch-action: auto;
      height: 100%;
      display: flex;
      user-select: none;
      scroll-snap-type: x mandatory; } }

@media print {
  main #columns {
    display: none; } }

@media screen {
  .night-mode main {
    background-color: #2e2b2c; } }

.list-leave-to {
  transform: translate(0%, -100%); }

.list-leave-active, .list-move {
  transition: transform 0.5s; }
