.article {
  color: unset;
  text-decoration: unset;
  display: block;
  background-color: white;
  width: 100%;
  cursor: pointer;
  user-select: none;
  margin-bottom: 12px;
  padding-bottom: 18px;
  box-shadow: 0 1px 2px rgba(144, 151, 156, 0.4);
  transition: box-shadow 0.2s ease-in-out;
  border-radius: 1px; }
  .article:hover {
    box-shadow: 0 1px 18px rgba(144, 151, 156, 0.4); }
  .article:not(.photo).flash {
    border-left: 12px solid #c9402d; }
  .article.alerte {
    border-left: 12px solid #fecb00; }
  .article.urgent {
    border-left: 12px solid #ffe065; }
  .article:visited {
    background-color: #f9fafb; }
    .article:visited h2, .article:visited p {
      color: #b5bfc5; }
  .article.canceled > h2 {
    text-decoration: line-through; }
  .article .cols {
    display: flex;
    justify-content: space-between;
    color: #a3acb1;
    padding: 18px 18px 0px 18px;
    font-size: 0.75rem; }
    .article .cols p.date {
      margin: 0px; }
      .article .cols p.date.embargo {
        color: #e88063; }
    .article .cols p.genre {
      text-transform: uppercase;
      font-weight: 600;
      margin: 0px; }
  .article .img-container + p {
    padding-top: 18px;
    margin-bottom: 0; }
  .article h2 {
    padding: 0 18px;
    font-size: 1.4rem;
    margin: 10px 0px;
    line-height: 1.4rem;
    letter-spacing: -0.045em;
    font-weight: 600;
    word-wrap: break-word; }
  .article .img-container {
    position: relative;
    width: 100%;
    height: 150px;
    background-size: 100% auto;
    background-position: center center;
    background-repeat: no-repeat;
    border-radius: 2px; }
    .article .img-container .UI-icon.UI-play-video {
      position: absolute;
      left: 0px;
      top: 50%;
      transform: translate(0%, -50%);
      font-size: 48px;
      color: white;
      opacity: 0.7;
      margin-left: 16px; }
  .article p.lead {
    padding: 0px 18px 12px;
    display: block;
    display: -webkit-box;
    margin-top: 0px;
    margin-bottom: 0px;
    word-wrap: break-word;
    overflow: hidden; }
  @media screen and (max-device-height: 568px) {
    .article p.lead {
      display: none; } }

@media screen {
  .night-mode .article {
    background-color: #231f20;
    box-shadow: 0 1px 6px rgba(0, 0, 0, 0.4); }
    .night-mode .article:hover {
      box-shadow: 0 1px 12px rgba(0, 0, 0, 0.6); }
    .night-mode .article h2 {
      color: #eee;
      letter-spacing: -0.6px;
      line-height: 23px; }
    .night-mode .article p.lead {
      color: #a3acb1; }
    .night-mode .article:visited {
      background-color: #2e2a2b; }
      .night-mode .article:visited h2, .night-mode .article:visited p {
        color: #90979c; } }
