.vue-recyclist {
  overflow-x: hidden;
  overflow-y: auto;
  overscroll-behavior-y: contain;
  contain: strict;
  -webkit-overflow-scrolling: touch;
  /* width */
  /* Track */
  /* Handle */
  /* Handle on hover */ }
  .vue-recyclist .vue-recyclist-items {
    position: relative; }
    .vue-recyclist .vue-recyclist-items .vue-recyclist-item {
      position: absolute;
      width: 100%; }
    .vue-recyclist .vue-recyclist-items .vue-recyclist-pool .vue-recyclist-item {
      visibility: hidden; }
  .vue-recyclist .vue-recyclist-nomore {
    overflow: hidden;
    margin: 10px auto;
    height: 20px;
    text-align: center; }
  .vue-recyclist::-webkit-scrollbar {
    width: 0.3em; }
  .vue-recyclist::-webkit-scrollbar-track {
    background: transparent; }
  .vue-recyclist::-webkit-scrollbar-thumb {
    background-color: #dae3e8;
    border-radius: 4px; }
  .vue-recyclist::-webkit-scrollbar-thumb:hover {
    background: #bbcbd5; }

.night-mode .vue-recyclist::-webkit-scrollbar-thumb {
  background-color: #211f1f; }

.night-mode .vue-recyclist::-webkit-scrollbar-thumb:hover {
  background: #060606; }

.night-mode .vue-recyclist .vue-recyclist-nomore {
  color: #90979c; }
